// import logo from './logo.svg';
import './App.css';
// import '/sass/style.scss';//src\assets\sass\style.css
// import './assets/css/style.css';

function TodoCommentContent(props) {
    return (
        <p className="pWidth fz-20">{props.message} </p>
    )
}

function TodoCommentSubtitle(props) {
    return (
        <p className="pWidth fz-20 bold">{props.message} </p>
    )
}

function TodoCommentUrl(props) {
    return (
        <p className="pWidth fz-20 ">{props.message.comment}
            (<a className="icon text-color-primary" href={props.message.coordinat} target="example">{props.message.coordinat}</a>), 
            <span> </span> 
            <a className="text-color-primary" href={props.message.url} target="example">{props.message.url}</a>
        </p>
    )
}

function TodoImage(props) {
    return(
        <p className="text-center">
            <img src={props.message} alt={props.location}/>
        </p>
    )
}


function App() {
    const text = [];
    let content = comment.content;
    // console.log(Object.keys(comment.content).length);
   
    for (let key in content) {
        let i = 0;
        if (content[key].text) {
            content[key].text.forEach(element => {
                if ((i === 0) || (i === 2) || (i === 4)) {
                    text.push(<TodoCommentSubtitle key={element} message={element}/>) 
                } else {
                    text.push(<TodoCommentContent key={element} message={element}/>)
                }
                i++;
            })
        };

        if (content[key].commentUrl) {
            let commentUrl = content[key].commentUrl;
            // console.log(commentUrl);
            text.push(<TodoCommentUrl key={commentUrl.coordinat} message={commentUrl}/>)

        }

        if (content[key].image) {
            let imageUrl = content[key].image.imageUrl;
            
            for (let index in imageUrl) {
                // console.log(imageUrl[index]);
                text.push(<TodoImage key={imageUrl[index]} message={imageUrl[index]} location = {content[key].image.location} />)
            };
        }

    };

    
    return(
        <div className="content"> 
            <div className="content-text">
                <p className="text-center fz-34 m20 bold">{comment.title}</p>
                {text}
            </div>
        </div> 
    )
}

export default App;


